.profile__subtitle {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 622px;
}

@media screen and (max-width: 850px) {
  .profile__subtitle {
    max-width: 440px;
  }
}

@media screen and (max-width: 767px) {
  .profile__subtitle {
    max-width: 250px;
    font-size: 14px;
    line-height: 17px;
    margin-top: 11px;
  }
}
